body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  background-color: #F7F7F7;
  height: 100vh;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.user-type-card {
  /* height: 284px; */
  height: 100%;
  width: 255px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 8px 8px 24px #090e1466;
  border-radius: 12px;
  opacity: 1;
  margin: auto;
  margin-top: -75px;
  padding: 10px;
  /* margin-bottom: 70px; */
}
.icon-container {
  background-color: #f7f7f7;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  margin-bottom: 5px;
}
.user-type-icon {
  margin: auto;
  width: 46px;
  height: 42px;
  /* background: #4385F6 0% 0% no-repeat padding-box; */
  opacity: 1;
  fill: #4385f6;
  /* color: #4385f6; */
}
.user-type-description {
  width: 90%;
  height: 70px;
  text-align: left;
  letter-spacing: 0px;
  color: #273b60;
  opacity: 1;
  margin-bottom: 2px;
  margin-top: 10px;
  margin-right: 15px;
  margin-left: 15px;
  font: normal normal normal 15px/20px Montserrat;
  overflow-y: auto;
}
.user-type-more-button {
  width: 119px;
  height: 28px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 8px 15px #727c8e5c;
  border-radius: 24px;
  opacity: 1;
  cursor: pointer;
  border: #fff;
  margin-right: 15px;
}
.user-type-more-button:hover {
transform: scale(1.1) ;
}
.more-text-usertype {
  text-align: center;
  font: normal normal normal 13px/15px Montserrat;
  letter-spacing: 0px;
  color: #727c8e;
  opacity: 1;
  margin: auto;
}
.more-button-arrow {
  width: 22px;
  height: 22px;
  color: #ffbe00;
  background: #fff 0% 0% no-repeat padding-box;
  border-radius: 24px;
  opacity: 1;
  margin: auto;
}

.user-type-label {
  text-align: left;
  letter-spacing: -0.4px;
  font: normal normal bold 18px/23px Montserrat;
  color: #273b60;
  opacity: 1;
  margin-left: 15px;
  margin-top: 30px;
  margin-bottom: 20px;
  /* overflow-x: auto; */
  height: 20px;
}

@media only screen and (max-width: 1191px) {
  .user-type-card{
    width: 30%;
  }
.icon-container{
  width: 70px;
  height: 70px;
}
  
}

@media only screen and (max-width: 578px) {
  .user-type-card{
    height: 100%;
  }
  .icon-container{
    width: 50px;
