  }
  .user-type-icon {
    width: 26px;
    height: 26px;
  }
  .user-type-label {
    font: normal normal bold 11px/16px Montserrat;
    margin-left: 5px;
    margin-top: 20px;
    margin-bottom:15px;
    overflow-y: auto;
  }
  .user-type-description{
    margin-left:5px;
    margin-right: 5px;
    font: normal normal normal 11px/15px Montserrat;
    overflow-y: auto;
  }
  .user-type-more-button {
    width: 70%;
  }
  .more-text-usertype {
    font: normal normal normal 11px/13px Montserrat;
  }
    
}

