.loading-scr {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background: #f5f5f5;
}

.load-bar {
    width: 150px;
    height: 2px;
    position: relative;
    overflow: hidden;
    background: #cfcfcf;
    margin-top: 22px;
}

.load-bar::before {
    content: "";
    width: 75px;
    height: 2px;
    position: absolute;
    left: -34px;
    background: #009688;
    animation: loadBar 1.5s infinite ease;
}

/*-- load bar animation CSS--*/
@keyframes loadBar {
    50% {
        left: 100px;
    }
}

::-webkit-scrollbar {
    width: 0.5em;
    background-color: #2b71d4;
    background-color: var(--primbgClr);
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
    background-color: #f0a945;
    background-color: var(--secdbgClr);
    outline: 1px solid slategrey;
}

.navbar-nav li.nav-item {
