/*ESERVICE-SIDENAV*/
.eservice-sidenav {
    height: 100vh;
    width: 0;
    position: fixed;
    z-index: 20000;
    top: 0;
    right: 0;
    background-color: #f3f2ef;
    background-color: var(--bkgColor);
    overflow-x: hidden;
    padding-top: 60px;
    transition: 0.5s;
}

/* Position and style the close button (top right corner) */
.eservice-sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 46px;
    margin-left: 50px;
    color: #f0a945;
    color: var(--secdbgClr);
    padding: 0px 0px 0px 6px;
    background-color: transparent;
}

/* Style page content - use this if you want to push the page content to the right when you open the side navigation */
#main {
    transition: margin-right 0.5s;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
    .eservice-sidenav {
        padding-top: 15px;
    }

    .eservice-sidenav a {
        font-size: 18px;
    }
}

/* Search view */

.search-dropdown {
    position: relative;
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    width: 460px;
    overflow: auto;
    padding: 10px;
    right: 0;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 3;
}

.search-dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.searchResultArea {
    max-height: 400px;
    overflow-y: auto;
}

/*.dropdown a:hover {background-color: #ddd;}*/

.showSearch-view {
    display: block;
}

.eservice-icon {
    /*font-size: x-large;*/
    font-size: 10px !important;

    display: block !important;
    text-align: center;
    margin: 5px auto;
    padding: 10px;
    border-radius: 5px;
    width: 100%;
    line-height: 2;
    text-transform: uppercase;
    background-color: rgba(19, 138, 146, 0.2);
}

.eservice-icon a {
    color: #6b8f92;
    color: var(--textPrimary);
    font-weight: 600;
}

.eservice-icon:hover {
    background-color: #f0a945;
    background-color: var(--secdbgClr);
    color: #fff;
}

.eservice-icon-detail {
    line-height: 17px;
    text-transform: uppercase;
    text-align: center !important;
}

.eservice-icon-detail > a {
    font-size: 10px !important;
    font-weight: 500;

    color: lightslategray;
}

.eservice-icon-detail:hover > a {
    color: #6b8f92;
    color: var(--textPrimary);
    text-transform: uppercase;
}

.showmoreEservice,
.hidemoreEservice,
.showmoremeservice,
.hidemoremeservice {
    font-size: 9px;
    text-align: center;
    font-weight: 400;
    cursor: pointer;
}

.social-media > a {
    color: #fff;
    /*background-color: var(--primbgClr);*/
    padding: 10px;
    margin: 10px 5px;
    font-size: 25px;
}

.sction {
    min-height: 80vh;
    padding-top: 70px !important;
}

.titleHolder {
    padding-top: 40px !important;
    padding-bottom: 40px;
    background-color: #282a2b;
    color: #fff;
}

.dropdown-menu > li > a {
    padding: 12px;
    font-size: 12px;
}

.dropdown-item.active,
.dropdown-item:active,
.dropdown-item:focus,
.dropdown-item:hover {
    background-color: #f0a945;
    background-color: var(--secdbgClr);
    color: #fff;
    color: var(--textwhite);
}

.dropdown-divider {
    margin: unset;
}

.dropdown-menu[data-bs-popper] {
    margin-top: unset !important;
    padding: 0px !important;
}

a.show {
    background-color: #f0a945;
    background-color: var(--secdbgClr);
    color: #fff;
    color: var(--textwhite);
}

@media all and (min-width: 992px) {
    .dropdown-menu li {
        position: relative;
        word-wrap: break-word;
    }

    .submenu li a {
        white-space: unset;
    }

    .nav-item .submenu {
        display: none;
        position: absolute;
        left: 100%;
        top: -10px;
        padding: 0px;
        width: 350px;
        max-height: 300px;
        overflow-y: auto;
        overflow-x: hidden;
        background-color: #fff;
    }

    .nav-item .submenu a {
        padding: 15px 10px;
    }

    .nav-item .submenu-left {
        right: 100%;
        left: auto;
    }

    .dropdown-menu > li:hover > .submenu {
        display: block;
    }
}

/* ============ desktop view .end// ============ */

/* ============ small devices ============ */
@media (max-width: 991px) {
    .dropdown-menu .dropdown-menu {
        margin-left: 0.7rem;
        margin-right: 0.7rem;
        margin-bottom: 0.5rem;
    }
}

.marquee {
    height: 50px;
    overflow: hidden;
    position: relative;
    background: #fefefe;
    color: #333;
    border: 1px solid #f0a945;
    border: 1px solid var(--secdbgClr);
}

.marquee p {
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    line-height: 50px;
    text-align: center;
    transform: translateX(100%);
    animation: scroll-left 20s linear infinite;
}

@keyframes scroll-left {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(-100%);
    }
}

.comminqueImg {
    /*width: fit-content;*/
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 500px;
}

