}

.dgi-slide .owl-prev {
    position: absolute;
    top: 45%;
    width: 55px;
    height: 55px;
    line-height: 40px;
    text-align: center;
    left: 30px;
    background: rgba(0, 0, 0, 0.38) none repeat scroll 0 0 !important;
    border-radius: 49%;
    color: #fff !important;
    z-index: 11111;
    transition: all 0.4s ease 0s;
}

.dgi-slide .owl-next {
    position: absolute;
    top: 45%;
    width: 55px;
    height: 55px;
    line-height: 40px;
    text-align: center;
    right: 30px;
    background: rgba(0, 0, 0, 0.38) none repeat scroll 0 0 !important;
    border-radius: 49%;
    color: #fff !important;
    z-index: 11111;
    transition: all 0.4s ease 0s;
}

.dgi-slide .owl-dots {
    position: absolute;
    z-index: 2;
    right: 0;
    top: 164px;
}

.dgi-slide .owl-dots .owl-dot {
    background: #f0a945;
    background: var(--secdbgClr);
    border-radius: 50%;
    border: 1px solid #2b71d4;
    border: 1px solid var(--primbgClr);
    padding: 5px !important;
    display: block;
    transition: 0.3s;
    margin: 9px 5px;
}

.owl-dot.active {
    border: 3px solid #fff !important;
    border: 3px solid var(--textwhite) !important;
    padding: 8px !important;
    transition: 0.33s;
}

#regimes {
    /*margin-top: -40px;*/
    /*position: relative;*/
    /*z-index: 5;*/
    text-align: center;
    padding-bottom: 40px;
    padding-top: 0px !important;
}

#regimes .container {
    padding: 20px 40px;
    border-bottom: 1px solid #c4c4c4;
    border-left: 1px solid #c4c4c4;
    border-right: 1px solid #c4c4c4;
    margin-bottom: 20px;
}

.lesRegime {
    border: 1px solid #d2d2d2;
    padding-top: 40px;
    padding-bottom: 40px;
    height: 300px;
}

.overlayRegime {
    padding-top: 40px;
    position: relative;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #3a526a;
    content: "";
    z-index: 1;
    box-sizing: border-box;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    opacity: 0;
    filter: alpha(opacity=0);
    -webkit-transition: opacity 0.35s;
    -webkit-transform: scale(0);
    transition: opacity 0.35s, transform 0.35s;
    transition: all ease 0.4s;
    transform: scale(0);
}

.first-show {
    background-color: #3a526a;
    padding: 20px 0px;
    transition: all ease 0.2s;
}

.first-show h3 > a {
    color: #fff !important;
}

.lesRegime:hover .first-show {
    transform: scale(0);
    display: none;
}

.lesRegime:hover .overlayRegime {
    transform: scale(1);
    opacity: 0.9;
}

.overlayRegime h1 {
    color: gray;
}

.lesRegime:hover {
    background-color: #2b71d4;
    background-color: var(--primbgClr);
}

.lesRegime:hover h1,
.lesRegime:hover a {
    color: #fff !important;
}

.lesRegime a {
    color: #2b71d4;
    color: var(--primbgClr);
    font-weight: 600;
}

.lesRegime a:hover {
    color: #f0a945;
    color: var(--secdbgClr);
    font-weight: 500;
}

/* les Service Utiles*/
#serviceUtiles {
    padding: 40px 0px;
    background-color: #d3d5d4;
}

.service-usuel__item-head {
    background-color: #282a2b;
    color: #fff;
    padding: 20px;
}

.service-usuel__item > a {
    background-color: #2258a6;
    background-color: var(--primbgdarkClr);
    color: #fff;
    margin-top: 15px;
}

.service-usuel__item .title {
    font-weight: bolder;
}

.service-usuel__item:hover .title {
    color: #f0a945;
    color: var(--secdbgClr);
}

.service-usuel__item:hover > a {
    position: relative;
    background-color: #f0a945;
    background-color: var(--secdbgClr);
    left: -15px;
}

.service-usuel__item::after {
    border-right: 3px solid #f0a945;
    border-right: 3px solid var(--secdbgClr);
    border-left: 3px solid #f0a945;
    border-left: 3px solid var(--secdbgClr);
    transform: scale(1, 0);
}

.service-usuel__item::before {
    border-top: 3px solid #f0a945;
    border-top: 3px solid var(--secdbgClr);
    border-bottom: 3px solid #f0a945;
    border-bottom: 3px solid var(--secdbgClr);
    transform: scale(0, 1);
}

.service-usuel__item::after,
.service-usuel__item::before {
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    content: "";
    opacity: 0;
    transition: opacity 0.35s, transform 0.35s;
}

.service-usuel__item:hover::after,
.service-usuel__item:hover::before {
    opacity: 1;
    transform: scale(1);
}

